.image {
  overflow: hidden;
  height: 300px;
  width: 100%;
  position: relative;
  cursor: pointer;
  margin-bottom: 30px;
  box-shadow: 0 0 25px 1px rgba(0, 0, 0, 0.3);
  transition: 0.5s;
  border-radius: 10px !important;

  background-color: transparent;
}

.image:after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 750px;
  height: 700px;
  -webkit-transform: translate(-140%, -50%);
          transform: translate(-140%, -50%);
  background-color: #48dbfb;
  background-image: linear-gradient(315deg, #48dbfb 0%, #d3d3d3 74%);

  border-radius: 50%;
  transition: 0.8s;
}

.image:hover:after {
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.image:hover img {
  -webkit-transform: translate(-50%, -50%) scale(1.3);
          transform: translate(-50%, -50%) scale(1.3);
}

.image img {
  position: absolute;
  height: 110%;
  top: 50%;
  border-radius: 5px !important;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: 0.8s;
}

.image i {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-2000px, -50%);
          transform: translate(-2000px, -50%);
  color: #fff;
  transition: 0.8s;
  transition-timing-function: ease-in;
}

.image:hover i {
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition-timing-function: ease;
}

