}
.caro-item {
  background-color: #fff;
  width: 280px;
  height: 320px;
  border-radius: 20px;
  overflow: hidden;
  margin-right: 10px;
  display: inline-block;
  cursor: pointer;
  transition: 450ms all;
  -webkit-transform-origin: center left;
          transform-origin: center left;
  position: relative;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.26);
}
.caro-item .panel1 {
  position: absolute;
  top: 0;
  right: -50%;
  width: 45px;
  background-color: transparent;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-flow: column;
          flex-flow: column;
  opacity: 1;
}
.caro-item:hover .panel1 {
  right: 0;
  opacity: 1;
  transition: 0.4s;
}
.caro-item .panel1 a {
  font-size: 20px;
  color: #b31b1b;
  margin: 15px 0;
}
.caro-item .panel1 a:hover {
  color: rgba(255, 0, 0, 0.9);
}

.caro-item .panel2 {
  position: absolute;
  bottom: 30%;
  right: 0px;
  width: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-flow: column;
          flex-flow: column;
  border-radius: 30px;
  background: #fff;
  box-shadow: 0 10px 10px 0 #e9ade9;
  border-radius: 200px 0px 200px 200px;
  -moz-border-radius: 200px 0px 200px 200px;
  -webkit-border-radius: 200px 0px 200px 200px;
  border: 0px solid #000000;
}
.caro-item .panel2 a {
  font-size: 18px;
  color: #1693d3;
  margin: 15px 0;
  text-decoration: none;
}
.caro-item .info {
  text-align: center;
